<template>
  <div class="content-div home-div" :class="page_status" id="content-div">
    <div class="header">
      <div class="pageBack-btn" @click="pageBack()">
        <img src="@/assets/images/icon-arrow.svg" height="25" alt="">
      </div>
      <span>{{pageName}}</span>
      
    </div>
    <div class="home-div-cover">
      <div v-for="i in 500" :key="i">{{watermark}}</div>
    </div>
    <div class="subPage-div" id="subPage-div">
      <div class="container-fluid">
        <div class="row">
          <div class="col-12">
            <div class="box">
              <div class="title">
              </div>
              <div class="content">
                <canvas id="subPageChart"></canvas>
              </div>
              <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊圖表顯示該區資訊</div>
            </div>
          </div>
          <div class="col-12">
            <div class="box">
              <div class="title">
                <!-- {{pageData[0][0]}} -->
              </div>
              <div class="content">
                <table class="customerTable">
                  <thead>
                    <tr>
                      <th style="text-align: center">處級名稱</th>
                      <th style="text-align: center">全球實銷佔比</th>
                    </tr>
                  </thead>
                  <tbody v-if="pageData != null">
                    <tr v-for="(key,index) in pageData[2][subPageId]" :key="index" @click="openSubPageInfo(index)">
                      <td align="center">{{key.name}}</td>
                      <td align="center">{{key.value}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊表格顯示詳細資訊</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'
var _chart01;

export default {
  components: {

  },
  props: {

  },
  watch: {
    pageData(newCount, oldCount) {
      if(newCount != oldCount){
        this.setChart();
      }
    }
        
  },
  computed: {
    page_status() {
      return this.$store.state.page_status;
    },
    subPageId(){
      return this.$store.state.subPageId
    },
    pageName(){
      if(this.$store.state.customerProfileData[this.$store.state.mainId].tableData != null){
        return this.$store.state.customerProfileData[this.$store.state.mainId].tableData[this.$store.state.pageId].name
      }else{
        return null
      }
    },
    pageData(){
      if(this.$store.state.customerProfileData[this.$store.state.mainId].tableData != null){
        return this.$store.state.customerProfileData[this.$store.state.mainId].tableData[this.$store.state.pageId].pageData;
      }else{
        return null
      }
    }
  },
  mounted:function(){
    this.initHeight();
    window.onresize = () => {
      this.initHeight();
    }
    this.setChart();
  },
  data(){
    return{
      watermark: this.$store.state.watermark,
      // tableDataStatus: 0
    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight;
      var hh = window.innerHeight - 50;
      document.getElementById('content-div').setAttribute("style","height:"+h+"px");
      document.getElementById('subPage-div').setAttribute("style","height:"+hh+"px");
    },
    pageBack(){
      this.$store.commit('closePage');
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      
      let chartColor = this.$store.state.chartColor;

      if(this.pageData!=null){
        var ctx01 = document.getElementById('subPageChart');
        _chart01 = new Chart(ctx01, {
            type: 'doughnut',
            data: {
                labels: this.pageData[0],
                datasets: [{
                    label: this.pageData[0],
                    data: this.pageData[1],
                    backgroundColor: chartColor,
                    borderColor: chartColor,
                    borderWidth: 0,
                }],
            },
            options: {
              aspectRatio: 2,
              legend: {
                labels: {
                    width: 50,
                    fontColor: '#fff',
                    fontFamily: 'Microsoft JhengHei'
                },
                position : "left"
              },
            }
        });
        
        // let _this = this;
        // ctx01.onclick = function(e) {
        //   var slice = _chart01.getElementAtEvent(e);
        //   if (!slice.length) return; 
        //   _this.$store.state.mainId = slice[0]._index;
        // }
      }
    },
    openSubPageInfo(id){
      console.log(id);
      
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="@/assets/css/page.css"></style>
<style scoped>
.content-div{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
  background: #212335;
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  /* background: linear-gradient(90deg,#212335,#4e505f,#212335); */
}

.content-div.active{
  left: 0;
}

.subPage-div{
  position: absolute;
  z-index: 1;
  top: 50px;
  width: 100%;
  left: 0;
  overflow-y: auto;
  overflow-x: hidden;
  /* background: #f00; */
}

.home-div-cover{
    position: absolute;
    z-index: 0;
    width: 200%;
    height: 100%;
    left: 0;
    top: 0px;
    overflow: hidden;
}

.home-div-cover div{
    position: relative;
    display: inline-block;
    width: 180px;
    text-align: center;
    line-height: 60px;
    font-size: 40px;
    font-weight: 700;
    color: rgba(255,255,255,0.05);
    transform: rotate(30deg);
}



.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

.header{
    position: absolute;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
    z-index: 1;
    /* background: #212335; */
    background: #181a2b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .pageBack-btn{
    position: relative;
    float: left;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .pageBack-btn:hover{
    background: #4e505f;
  }

  .pageBack-btn img{
    transform: rotate(-90deg);
  }

</style>
