<template>
  <div class="container-fluid">
    <div class="row pb-3">
      <div class="col-12 mt-3">
        <h2>Customer Profile</h2>
      </div>
      
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4">
        <div class="box">
          <div class="content">
            <canvas id="chartCustomerProfile"></canvas>
          </div>
          <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊圖表顯示該區資訊</div>
        </div>
      </div>
      <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-8">
        <div class="box">
          <div class="title">
            {{customerProfileData[mainId].name}}
          </div>
          <div class="content">
            <table class="customerTable">
              <thead>
                <tr>
                  <th style="text-align: center">處級名稱</th>
                  <th style="text-align: center">全球實銷佔比</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(key,index) in customerProfileData[mainId].tableData" :key="index" @click="openCustomerProfileSubPage(index)">
                  <td align="center">{{key.name}}</td>
                  <td align="center">{{key.value}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.2)">點擊表格顯示詳細資訊</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js'

var _chart01;

export default {
  props: {
  },
  mounted:function(){ 
    this.init() 
  },
  computed: {
    customerProfileData() {
      return this.$store.state.customerProfileData;
    },
    mainId(){
      return this.$store.state.mainId
    }
  },
  data(){
    return {

    }
  },
  methods:{
    init(){
      this.setChart();
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      let chartData = [[],[]];
      let chartColor = this.$store.state.chartColor;

      for(var key in this.customerProfileData){
        chartData[0].push(this.customerProfileData[key].name)
        chartData[1].push(this.customerProfileData[key].value)
      }

      var ctx01 = document.getElementById('chartCustomerProfile');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: chartData[0],
              datasets: [{
                  label: chartData[0],
                  data: chartData[1],
                  backgroundColor: chartColor,
                  borderColor: chartColor,
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });
      let _this = this;
      ctx01.onclick = function(e) {
        var slice = _chart01.getElementAtEvent(e);
        if (!slice.length) return; // return if not clicked on slice
        _this.$store.state.mainId = slice[0]._index;
      }
    },
    openCustomerProfileSubPage(pageId){
      this.$store.commit('openPage');
      this.$store.state.pageId = pageId;
        //log 目前的狀態
        // this.$store.commit('logAllStatus');

    }
  }
}
</script>

