<template>
  <div class="content-div home-div" id="content-div">
    <transition name="fade">
      <div class="window-box-cover" @click="closeBox" v-if="noticestatus">
        <div class="window-box">
          123
        </div>
      </div>  
    </transition>
    <transition name="fade">
      <div class="window-box-cover" @click="closeBox" v-if="userstatus">
        <div class="window-box">
          user
        </div>
      </div>  
    </transition>
    <div class="home-div-cover">
      <div v-for="i in 500" :key="i">{{watermark}}</div>
    </div>
    <transition name="fade">
      <SalesInsight class="page" v-if="pageData[0].status"/>
    </transition>
    <transition name="fade">
      <CustomerProfile class="page" v-if="pageData[1].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[2].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[3].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[4].status"/>
    </transition>
    <transition name="fade">
      <Page404 class="page" v-if="pageData[5].status"/>
    </transition>
    <transition name="fade">
      <PowerBi class="page" v-if="pageData[6].status"/>
    </transition>
  </div>
</template>

<script>
import SalesInsight from '../components/page/SalesInsight'
import CustomerProfile from '../components/page/CustomerProfile'
import PowerBi from '../components/page/PowerBi'
import Page404 from '../components/page/page404'

export default {
  components: {
    SalesInsight,
    CustomerProfile,
    PowerBi,
    Page404,
  },
  props: {
    nowstatus: String,
    noticestatus: Boolean,
    userstatus: Boolean
  },
  watch:{
		nowstatus(){
      this.changeHome(this.nowstatus);
    }
	},
  mounted:function(){
    this.initHeight();
    this.initStatus(this.nowstatus)
    window.onresize = () => {
      this.initHeight();
    }
  },
  data(){
    return{
      pageStatus: this.nowstatus, 
      watermark: this.$store.state.watermark,
      pageData: this.$store.state.mainPageData,
    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight - 50;
      document.getElementById('content-div').setAttribute("style","height:"+h+"px");
    },
    initStatus(id){
      this.changeHome(id);
    },
    changeHome(id){
      for(let key in this.pageData){
        if(id == this.pageData[key].id){
          this.pageData[key].status = true;
        }else{
          this.pageData[key].status = false;
        }
      }
    },
    closeBox(){
      this.$emit('userStatus',false);
      this.$emit('noticeStatus', false);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content-div{
  position: relative;
  width: 100%;
  height: calc(100vh - 50px);
  overflow-y: auto;
  overflow-x: hidden;
  background: #4e505f;
  background: linear-gradient(90deg,#212335,#4e505f,#212335);
}

.page{
  position: absolute;
}

.fade-enter-active, .fade-leave-active {
  transition: 0.7s all;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.home-div-cover{
    position: fixed;
    z-index: 0;
    width: 200%;
    height: 100%;
    left: 0;
    top: 50px;
    overflow: hidden;
}

.home-div-cover div{
    position: relative;
    display: inline-block;
    width: 180px;
    text-align: center;
    line-height: 60px;
    font-size: 40px;
    font-weight: 700;
    color: rgba(255,255,255,0.05);
    transform: rotate(30deg);
}



.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

</style>
