<template>
  <div class="header">
    <div class="menu-btn" @click="callMenu()">
      <img src="@/assets/images/icon-nav.svg" height="25" alt="">
    </div>
    <img src="@/assets/images/logo.png" height="25">
    <div class="user-btn" @click="callUser()">
      <img src="@/assets/images/icon-user.svg" height="25" alt="">
    </div>
    <div class="notice-btn" @click="callNotice()">
      <img src="@/assets/images/icon-notice.svg" height="25" alt="">
      <span v-if="noticeShow"></span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    menuOpenStatus: String,
    userOpenStatus: Boolean,
    noticeOpenStatus: Boolean,
  },
  data(){
    return {
      menuStatus: this.menuOpenStatus,
      userStatus: this.userOpenStatus,
      noticeStatus: this.noticeOpenStatus,
      noticeShow: true  
    }
  },
  watch:{
    menuOpenStatus(){
      this.menuStatus =  this.menuOpenStatus;
    },
    userOpenStatus(){
      this.userStatus =  this.userOpenStatus;
    },
    noticeOpenStatus(){
      this.noticeStatus =  this.noticeOpenStatus;
    }
	},
  methods:{
    callMenu(){
      if(this.menuStatus == 'active'){
        this.menuStatus = '';
      }else{
        this.menuStatus = 'active';
      }
      this.$emit('menuStatus', this.menuStatus)
    },
    
    callUser(){
      if(this.userStatus == true){
        this.userStatus = false;
      }else{
        this.userStatus = true;
      }
      this.$emit('userStatus', this.userStatus)
    },

    callNotice(){
      this.noticeShow = false;
      if(this.noticeStatus == true){
        this.noticeStatus = false;
      }else{
        this.noticeStatus = true;
      }
      this.$emit('noticeStatus', this.noticeStatus)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @keyframes flickerAnimation {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }
  .header{
    position: relative;
    width: 100%;
    height: 50px;
    /* background: #212335; */
    background: #181a2b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .menu-btn{
    position: relative;
    float: left;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }
  .notice-btn,
  .user-btn{
    position: relative;
    float: right;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .menu-btn:hover,
  .user-btn:hover,
  .notice-btn:hover{
    background: #4e505f;
  }

  .notice-btn span{
    position: absolute;
    right: 10px;
    top: 10px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #f00;
    animation: flickerAnimation 1s infinite;
  }

</style>
